import React from 'react';
import cx from 'classnames';
import ArchiveCover from '../ArchiveCover/ArchiveCover';
import ArchiveLine from '../ArchiveLine/ArchiveLine';
import ToggleButton, {SideToggleButton} from '../ToggleButton';
import s from './SidebarArchives.scss';
import {ArchiveHomeInfo, ArchiveObjectContainer} from "../../types";

export enum listView { cover, list }

export const listViewOptions = Object.keys(listView).filter((item) => {
  return isNaN(Number(item));
});

export const listViewLength = listViewOptions.length;

const buttonSize = 32;

interface SideOptionsBarProps {
  showContent: boolean;
  topSkip: number;
}

const styleSideList = {position: 'absolute' as any, width: '35px'};

const SideOptionsBar: React.FC<SideOptionsBarProps> = ({ showContent, topSkip, children }) => {
  let style;

  if(showContent) {
    style = Object.assign({}, styleSideList, {right: '-36px'})
  } else {
    style = Object.assign({}, styleSideList, {left: '0'})
  }

  style.top = `${topSkip}px`

  return (<div style={style}>{children}</div>)
};

const getImageElement = (viewMode: listView, archive: ArchiveHomeInfo) => {
  switch (viewMode) {
    case listView.cover:
      return <ArchiveCover
        archive={archive}
      />;
    case listView.list:
      return <ArchiveLine
        archive={archive}
      />;
    default:
      return <ArchiveCover
        archive={archive}
      />;
  }
}

const getViewIcon = (viewMode: listView) => {
  switch (viewMode) {
    case listView.cover:
      return <i className="fas fa-solid fa-images"></i>;
    case listView.list:
      return <i className="fas fa-solid fa-list"></i>;
    default:
      return <i className="fas fa-solid fa-images"></i>;
  }
}

const getExtractedIcon = (extractedOnly: boolean) => {
  return extractedOnly ? <i className="fas fa-solid fa-file-archive"></i> : <i className="fas fa-regular fa-file-archive"></i>
}

const getViewListClass = (viewMode: listView) => {
  switch (viewMode) {
    case listView.cover:
      return s.archCont;
    case listView.list:
      return s.archContList;
    default:
      return s.archCont;
  }
}

interface Props {
  archives: ArchiveObjectContainer;
  displayQueue: boolean;
  toggleQueue: () => void;
  className?: string;
  handleGoToPage: (archivePage: number) => void;
  viewMode: listView;
  handleViewMode: () => void;
  extractedOnly: boolean;
  handleToggleExtractedOnly: () => void;
}

const SidebarArchives = (
  {displayQueue, archives, toggleQueue, className,
    handleGoToPage, viewMode, handleViewMode, extractedOnly, handleToggleExtractedOnly}: Props): JSX.Element => (
  <div className={cx(className, displayQueue ? getViewListClass(viewMode) : null)}>
    <div className={s.overflowScroll}>
      {
        displayQueue ? archives.objects.map(
          (archive) => (
            <div key={archive.id}>
              {getImageElement(viewMode, archive)}
            </div>
          ),
        ) : null
      }
    </div>
    <div>
      <ToggleButton onClick={toggleQueue} showContent={displayQueue} alignRight={false}/>
      {
        displayQueue ?
          <SideOptionsBar showContent={displayQueue} topSkip={buttonSize}>
            <div>
              <SideToggleButton
                onClick={() => handleGoToPage(1)}
                staticContent={''}
              />
              <SideToggleButton
                onClick={() => handleGoToPage(archives.has_previous ? archives.number - 1 : 1)}
                staticContent={''}
              />
              <SideToggleButton
                onClick={() => handleGoToPage(archives.has_next ? archives.number + 1 : archives.number)}
                staticContent={''}
              />
              <SideToggleButton
                onClick={() => handleGoToPage(archives.num_pages)}
                staticContent={''}
              />
            </div>
            <SideToggleButton
              onClick={() => handleGoToPage(archives.num_pages)}
              staticContent={''}
            />
            <SideToggleButton
              onClick={() => handleToggleExtractedOnly()}
              staticContent={getExtractedIcon(extractedOnly)}
              marginTop={3}
              color={'success'}
            />
            <SideToggleButton
              onClick={() => handleViewMode()}
              staticContent={getViewIcon(viewMode)}
              color={'success'}
            />

          </SideOptionsBar>
          : null
      }
    </div>
  </div>
);

export default SidebarArchives;