import React from 'react';
import { Button } from 'reactstrap';

interface Props {
  onClick: () => void;
  showContent: boolean;
  alignRight: boolean;
}

const commonStyle = {position: 'absolute', top: '0px'};

const ToggleButton = ({ onClick, showContent, alignRight }: Props): JSX.Element => {
  let style;
  const showContentIcon = alignRight ? '>' : '<';
  const hideContentIcon = alignRight ? '<' : '>';
  if(showContent) {
    style = Object.assign({}, commonStyle, alignRight ? {left: '-30px'} : {right: '-30px'})
  } else {
    style = Object.assign({}, commonStyle, alignRight ? {right: '0'} : {left: '0'})
  }
  return (<Button onClick={onClick} size="sm" style={style}>{showContent ? showContentIcon : hideContentIcon }</Button>)
};

interface PaginationProps {
  onClick: () => void;
  staticContent: React.ReactNode;
  marginTop?: number;
  color?: string;
}

export const SideToggleButton = ({ onClick, staticContent, marginTop = 1, color = 'info' }: PaginationProps): JSX.Element => (
  <Button
    color={color} onClick={onClick}
    size="sm" style={{width: '100%', marginTop: marginTop}}>
    {staticContent}
  </Button>
);

export default ToggleButton;